function ajaxFundDividendTable (){
  var deferred = $.Deferred();

  var postdata = {};
  postdata.companyId = filterXSS(sessionStorage.getItem("fundCompany")) || $("#select-company").val();
  postdata.typeId = filterXSS(sessionStorage.getItem("fundType")) || $("#select-type").val();
  postdata.dividendYear = filterXSS(sessionStorage.getItem("fundYear")) || $("#select-year").val();
  if(!_.isEmpty(sessionStorage.getItem("fundKeyword"))){
    postdata.keyword = sessionStorage.getItem("fundKeyword") || $("#input-keyword").val();
  }

  $.ajax({
      url: Constants.getFundDividendTableUrl(),
      type:"GET",
      data:postdata
  }).done(function(response){
    var fundDividendList = response,
        fundTypeHtml = [];
    $.each(fundDividendList, function(index,fundDividendItem) {

      var dividendsHtml = [],
      cxBgcolor = "bgcolor-" + fundDividendItem.MoneyTypeId.toLowerCase();
      
      if(_.isEmpty(fundDividendItem.Dividends)) return; // continue

      $.each(fundDividendItem.Dividends,function(index,dividendsItem){
        var dividendDate = new Date(dividendsItem.DividendDate),
            fmtDividendDate = dividendDate.format("yyyy/mm");
        dividendsHtml.push("<tbody>");
        dividendsHtml.push("  <td>" + fmtDividendDate + "</td>");
        dividendsHtml.push("  <td>" + dividendsItem.DividendPerUnit + "</td>");
        dividendsHtml.push("  <td>" + dividendsItem.MonthDividendRate + "</td>");
        dividendsHtml.push("  <td>" + dividendsItem.MonthReturnRate + "</td>");
        dividendsHtml.push("  <td>" + dividendsItem.YearDividendRate + "</td>");
        dividendsHtml.push("</tbody>");
      }); // end each fundDividendItem.Dividends

      fundTypeHtml.push("<div class='js-fund-item' data-fundid='" + fundDividendItem.FundId + "''>");
      fundTypeHtml.push("<div data-role='headline'>");
      fundTypeHtml.push(" <div class='shortname'>" + fundDividendItem.FundShortName + "</div>");
      fundTypeHtml.push("</div>");
      fundTypeHtml.push("<div data-role='moneytype'>");
      fundTypeHtml.push(" <div class='item " + cxBgcolor + "'>" + fundDividendItem.MoneyTypeName + "</div>");
      fundTypeHtml.push("</div>");        
      fundTypeHtml.push("<table data-role='table'>");
      fundTypeHtml.push(" <thead>");
      fundTypeHtml.push("   <th>配息<br>期間</th>");
      fundTypeHtml.push("   <th>每單位<br>配息<br>金額</th>");
      fundTypeHtml.push("   <th>當月<br>配息率<br>(%)</th>");
      fundTypeHtml.push("   <th>當月<br>報酬率<br>(含息)%</th>");
      fundTypeHtml.push("   <th>年化<br>配息率<br>(%)</th>");
      fundTypeHtml.push(" </thead>");
      fundTypeHtml.push(  dividendsHtml.join(" "));        
      fundTypeHtml.push("</table>");
      fundTypeHtml.push("</div>");

    }); // end each fundDividendList

    $("#result-block").removeClass();
    if(fundTypeHtml && fundTypeHtml.length > 0){
      $("#result-block").addClass('has-data').html(fundTypeHtml.join(" "));
    }else{
      $("#result-block").addClass('empty-data').html("<div class='no-dividend'>無配息資料</div>");
    }

    $("#result-block [data-role=headline]").each(function(index){
        var shortnameHeight = $(this).find(".shortname").outerHeight(true);
        $(this).css("height",shortnameHeight);
    });

    deferred.resolve();

  });

  return deferred.promise();
}


$(document).on( "pageinit", "#page-fund-dividend", function(envent) {

	var nowyear = new Date().getFullYear(),
      lastYear = nowyear - 1; 

  //使用Derffered Ojbect讓資料載入後，再解除Loading
  var dLoadCompany = $.Deferred(),
      dLoadType = $.Deferred();

  App.StartLoading();  

  $(".numura-warning").loadWarning();

  $("#input-keyword").val(sessionStorage.getItem("fundKeyword"));    

	$("#select-year")
	.empty()
	.append($('<option>', { value:lastYear,text :lastYear }))
	.append($('<option>', { value : nowyear,text :nowyear,selected:true }))
	.selectmenu("refresh",true)
  .val(nowyear); 

  $("#select-company").loadSelectFundCompany(
    {
      "isretail":true,
      "success":function(){
        dLoadCompany.resolve("success");
      },"failure":function(){
        dLoadCompany.reject("failure");
      }
    }
  );

  $("#select-type").loadSelectFundType(
    {
      "includeFundTypeAll":false,
      "success":function(){
        dLoadType.resolve("success");
      },"failure":function(){
        dLoadType.reject("failure");
      }      
    }
  );

  $.when(dLoadCompany,dLoadType)
    .then(function(){
      if(sessionStorage.getItem("fundCompany")){
        $("#select-company")
          .val(sessionStorage.getItem("fundCompany"))
          .selectmenu("refresh",true);
      }
      if(sessionStorage.getItem("fundType")){
        $("#select-type")
          .val(sessionStorage.getItem("fundType"))
          .selectmenu("refresh",true);
      }
      if(sessionStorage.getItem("fundYear")){
        $("#select-year")
          .val(sessionStorage.getItem("fundYear"))
          .selectmenu("refresh",true);
      }      
      //ajaxFundDividendTable();
      // $.when(ajaxFundDividendTable()).done(function(){
      //   App.StopLoading();
      // });      
    }).done(function(){
      App.StopLoading();
  });

});

$(document).on("vclick",".js-fund-item",function(event){
  sessionStorage.setItem("fundid",$(this).data("fundid"));
  location.href = "../html/singleFundInfo.html";
});

$(document).on("vclick","#btn-submit",function(event){
  var pattern = /^[A-z0-9 \u4e00-\u9fa5]+$/;
  var keyword = $("#input-keyword").val();
  if(keyword.length > 0 && !pattern.test(keyword)){
    App.ShowAlert('請勿輸入符號或全形英數',"","確定");
    return false;
  }

  sessionStorage.setItem("fundCompany",$("#select-company").val());
  sessionStorage.setItem("fundType",$("#select-type").val());
  sessionStorage.setItem("fundYear",$("#select-year").val());
  sessionStorage.setItem("fundKeyword",keyword);

  App.StartLoading(); 
  
  $.when(ajaxFundDividendTable()).done(function(){
      App.StopLoading();
  });
  
});